export function setToken(token) {
	uni.setStorage({
		key: 'storage_Token',
		data: token,
		success: function () {
			console.log(token,'success');
		}
	})
} 
export function getToken() {
	uni.getStorage({
		key: 'storage_Token',
		success: function (res) {
			console.log(res.data,'getToken');
		}
	})
}
export function removeToken(token) {
	uni.removeStorage({
		key: 'storage_Token',
		data: token,
		success: function (res) {
			console.log('removesuccess');
		}
	})
}